import React, { useState } from "react";
import { ResponsiveLine } from '@nivo/line'
import { useSelector } from "react-redux";
import { useEffect } from "react";
import Button from 'react-bootstrap/Button';
import Accordion from 'react-bootstrap/Accordion';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Card from 'react-bootstrap/Card';


export default function SpreadMKrGraph() {
    //Url state가져오는 코드
    const localUrl = useSelector((state) => state.localUrl)
    const itemUrl10_2 = '/econ/spread/10-2/m/kr'
    const itemUrl10_91 = '/econ/spread/10-91/m/kr'


    //fetch로 가져온 data 임시저장
    const [data, setData] = useState([])

    //시작과 끝 날짜 저장
    const [startDate, setStartDate] = useState('')
    const [endDate, setEndDate] = useState('')

    //data 테스트 버튼 함수
    async function testData() {
        console.log(data)
    }

    try {
        //처음 렌더링시 fetch 함수
        useEffect(() => {
            async function getData() {
                let fetch10_91 = await fetch(localUrl + itemUrl10_91)
                    .then((res) => res.json())

                let fetch10_2 = await fetch(localUrl + itemUrl10_2)
                    .then((res) => res.json())

                //시작과 끝 날짜 추출
                setStartDate(fetch10_91[0]['data'][0]['x'])
                setEndDate(fetch10_91[0]['data'][fetch10_91[0]['data'].length - 1]['x'])

                //색상 변경 코드
                fetch10_91[0]['color'] = 'hsl(243, 70%, 50%)'
                fetch10_2[0]['color'] = 'hsl(238, 70%, 50%)'

                const result = [
                    fetch10_91[0],
                    fetch10_2[0],
                ]

                setData(result)
            };
            getData()
        }, [])

    } catch (err) {
        console.log('10년-2년 금리차 렌더링 오류')
    }

    const SpreadMKrGraph

        = ({ data }) => (
            <ResponsiveLine
                data={data}
                margin={{ top: 80, right: 140, bottom: 100, left: 60 }}
                xScale={{ type: 'point' }}
                yScale={{
                    type: 'linear',
                    min: 'auto',
                    max: 'auto',
                    stacked: false,
                    reverse: false
                }}
                yFormat=" >-.2f"
                axisTop={null}
                axisRight={null}
                axisBottom={{
                    orient: 'bottom',
                    tickSize: 5,
                    tickPadding: 5,
                    tickRotation: -80,
                    legend: '',
                    legendOffset: 50,
                    legendPosition: 'middle'
                }}
                axisLeft={{
                    orient: 'left',
                    tickSize: 5,
                    tickPadding: 5,
                    tickRotation: 0,
                    legend: '',
                    legendOffset: -40,
                    legendPosition: 'middle'
                }}
                colors={{ scheme: 'category10' }}
                lineWidth={4}
                pointSize={6}
                pointColor={{ from: 'color', modifiers: [] }}
                pointBorderWidth={2}
                pointBorderColor={{ from: 'serieColor' }}
                pointLabelYOffset={-12}
                enableSlices="x"
                useMesh={true}
                legends={[
                    {
                        anchor: 'bottom-right',
                        direction: 'column',
                        justify: false,
                        translateX: 100,
                        translateY: 0,
                        itemsSpacing: 0,
                        itemDirection: 'left-to-right',
                        itemWidth: 80,
                        itemHeight: 20,
                        itemOpacity: 0.75,
                        symbolSize: 12,
                        symbolShape: 'circle',
                        symbolBorderColor: 'rgba(0, 0, 0, .5)',
                        effects: [
                            {
                                on: 'hover',
                                style: {
                                    itemBackground: 'rgba(0, 0, 0, .03)',
                                    itemOpacity: 1
                                }
                            }
                        ]
                    }
                ]}
            />
        )
    return (
        <>

            <Container>
                <Card border="light">
                    <Card.Body>
                        <Card.Title className="text-center">한국의 장단기 금리차 [3개월]</Card.Title>
                        <Card.Text className="text-center">
                            {startDate} ~ {endDate}
                        </Card.Text>
                        {/* <Button variant="primary" onClick={testData}>Primary</Button>{' '} */}
                        <div style={{ width: 'auto', height: '400px', margin: '0 auto' }}>
                            <SpreadMKrGraph data={data} />
                        </div>
                        <Row>
                            <Col></Col>
                            <Col lg="8">
                                <Accordion>
                                    <Accordion.Item eventKey="0">
                                        <Accordion.Header>장단기금리역전</Accordion.Header>
                                        <Accordion.Body>
                                            단기 금리가 장기 금리보다 높은 현상. <br />
                                            즉 만기가 짧은 채권의 금리가 만기가 긴 채권 금리보다 높아지는 현상이다. <br />
                                            일반적으로 채권시장에선 돈을 빌리는 기간이 길수록 금리가 높아지지만, <br />
                                            장단기 국채 금리가 역전되는 상황이 오면 ‘경기 침체’의 전조 현상으로 판단한다.<br />
                                            장단기 금리 역전은 주요 금융시장 및 경제지표 중에서 경기침체에 대한 예측력이 가장 정확한 지표 중 하나로 알려져 있는데, <br />
                                            실제로 미국에서는 1960년 이후에 발생한 모든 경기침체에 앞서 장단기 금리가 역전된 바 있다.<br />
                                            장단기 금리를 비교할 때 주로 언급되는 금리는 미국채 10년물과 2년물이다.
                                        </Accordion.Body>
                                    </Accordion.Item>
                                    <Accordion.Item eventKey="1">
                                    <Accordion.Header>개발자의 첨언</Accordion.Header>
                                        <Accordion.Body>
                                            한국의 경우 국고채 3개월물이 없어 양도성예금증서(CD, 3개월)를 기준으로 지수를 만들었습니다.<br />
                                        </Accordion.Body>
                                    </Accordion.Item>
                                    <Accordion.Item eventKey="2">
                                    <Accordion.Header>자료 출처</Accordion.Header>
                                        <Accordion.Body>
                                            한국은행 경제통계시스템 <br/>
                                            시장금리[817Y002] 국고채 10년[010210000], 국고채 2년[010195000], CD(91일)[010502000]
                                        </Accordion.Body>
                                    </Accordion.Item>
                                    
                                </Accordion>
                            </Col>
                            <Col></Col>
                        </Row>
                    </Card.Body>
                </Card>
            </Container>
        </>
    )
}