import React, { useState } from "react";
import { ResponsiveLine } from '@nivo/line'
import { useSelector } from "react-redux";
import { useEffect } from "react";
import Accordion from 'react-bootstrap/Accordion';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Card from 'react-bootstrap/Card';


export default function EsiNsiGraph() {
    const localUrl = useSelector((state) => state.localUrl)
    const itemUrl = '/econ/bok/esi_nsi'

    const [data, setData] = useState([])

    //시작과 끝 날짜 저장
    const [startDate, setStartDate] = useState('')
    const [endDate, setEndDate] = useState('')

    try {
        useEffect(() => {
            async function getEsiNsi() {
                let result = await fetch(localUrl + itemUrl)
                    .then((res) => res.json())

                //시작과 끝 날짜 추출
                setStartDate(result[0]['data'][0]['x'])
                setEndDate(result[0]['data'][result[0]['data'].length - 1]['x'])

                result[0]['color'] = 'hsl(50, 70%, 50%)'
                result[1]['color'] = 'hsl(328, 70%, 50%)'

                setData(result)
            };
            getEsiNsi()
        }, [])

    } catch (err) {
        console.log('경제,뉴스 심리지수 렌더링 오류')
    }





    const EsiNsiGraph = ({ data }) => (
        <ResponsiveLine
            data={data}
            margin={{ top: 50, right: 110, bottom: 100, left: 60 }}
            xScale={{ type: 'point' }}
            yScale={{
                type: 'linear',
                min: 'auto',
                max: 'auto',
                stacked: false,
                reverse: false
            }}
            yFormat=" >-.2f"
            axisTop={null}
            axisRight={null}
            axisBottom={{
                orient: 'bottom',
                tickSize: 5,
                tickPadding: 5,
                tickRotation: -80,
                legend: '',
                legendOffset: 50,
                legendPosition: 'middle'
            }}
            axisLeft={{
                orient: 'left',
                tickSize: 5,
                tickPadding: 5,
                tickRotation: 0,
                legend: '',
                legendOffset: -40,
                legendPosition: 'middle'
            }}
            colors={{ scheme: 'category10' }}
            lineWidth={3}
            pointSize={5}
            pointColor={{ from: 'color', modifiers: [] }}
            pointBorderWidth={2}
            pointBorderColor={{ from: 'serieColor' }}
            pointLabelYOffset={-12}
            enableSlices="x"
            useMesh={true}
            legends={[
                {
                    anchor: 'bottom-right',
                    direction: 'column',
                    justify: false,
                    translateX: 100,
                    translateY: 0,
                    itemsSpacing: 0,
                    itemDirection: 'left-to-right',
                    itemWidth: 80,
                    itemHeight: 20,
                    itemOpacity: 0.75,
                    symbolSize: 12,
                    symbolShape: 'circle',
                    symbolBorderColor: 'rgba(0, 0, 0, .5)',
                    effects: [
                        {
                            on: 'hover',
                            style: {
                                itemBackground: 'rgba(0, 0, 0, .03)',
                                itemOpacity: 1
                            }
                        }
                    ]
                }
            ]}
        />
    )
    return (
        <>

            <Container>
                <Card border="light">
                    <Card.Body>
                        <Card.Title className="text-center">경제심리지수와 뉴스심리지수 [5년]</Card.Title>
                        <Card.Text className="text-center">
                            {startDate} ~ {endDate}
                        </Card.Text>
                        <div style={{ width: 'auto', height: '400px', margin: '0 auto' }}>
                            <EsiNsiGraph data={data} />
                        </div>
                        <Row>
                            <Col></Col>
                            <Col lg="8">
                                <Accordion>
                                    <Accordion.Item eventKey="0">
                                        <Accordion.Header>경제심리지수</Accordion.Header>
                                        <Accordion.Body>
                                            경제심리지수(ESI, Economic Sentiment Index)는 기업경기실사지수와 소비자동향지수를 합성하여 기업과 소비자 모두를 포함한 민간의 경제 상황에 대한 심리를 종합적으로 파악하기 위한 지표이다.<br />
                                            한국은행이 2012년 6월부터 발표하고 있으며, 2003년 1월부터 2012년 12월까지 ESI의 평균을 100으로 한다. <br />
                                            이를 기준으로 100을 넘으면 민간의 경제심리가 과거보다 좋다는 의미이고, 100을 밑돌면 과거 평균보다 못하다는 뜻이다.
                                        </Accordion.Body>
                                    </Accordion.Item>
                                    <Accordion.Item eventKey="1">
                                        <Accordion.Header>뉴스심리지수</Accordion.Header>
                                        <Accordion.Body>
                                            뉴스심리지수(NSI, News Sentiment Index)는 한국은행이 인터넷 경제뉴스 데이터를 문장 단위의 긍정·부정·중립 감성으로 분류하고, <br />
                                            뉴스 기사에 나타난 경제 심리를 머신러닝을 이용해 긍정과 부정 문장의 차이를 지수화한 것이다. <br/>
                                            뉴스심리지수는 100을 초과하면 긍정문장이 더 많고, 100보다 아래면 부정문장이 더 많음을 뜻한다.
                                        </Accordion.Body>
                                    </Accordion.Item>
                                    <Accordion.Item eventKey="2">
                                        <Accordion.Header>자료 출처</Accordion.Header>
                                        <Accordion.Body>
                                            한국은행 경제통계시스템 <br/>
                                            경제심리지수[513Y001] 순환변동치[E2000] <br/>
                                            뉴스심리지수[521Y001] [A001]
                                        </Accordion.Body>
                                    </Accordion.Item>
                                </Accordion>
                            </Col>
                            <Col></Col>
                        </Row>
                    </Card.Body>
                </Card>
            </Container>
        </>
    )
}