import React, { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import Card from 'react-bootstrap/Card';
import Button from 'react-bootstrap/Button';
import ListGroup from 'react-bootstrap/ListGroup';
import Container from "react-bootstrap/esm/Container";
import Row from "react-bootstrap/esm/Row";
import Col from "react-bootstrap/esm/Col";


export default function OverView() {

    const overView = useSelector((state: any) => state.overView)

    const [법인구분, set법인구분] = useState('')
    const [설립일, set설립일] = useState('')
    const [메인페이지, set메인페이지] = useState('')
    const [IR페이지, setIR페이지] = useState('')
    const [법인등록번호, set법인등록번호] = useState('')
    const [사업자등록번호, set사업자등록번호] = useState('')

    const [linkActivated, setLinkActivated] = useState<boolean>(true)
    const [irLinkActivated, setIrLinkActivated] = useState<boolean>(true)


    useEffect(() => {
        if (overView.corp_cls === 'Y') {
            set법인구분('유가증권시장(코스피)')
        } else if (overView.corp_cls === 'K') {
            set법인구분('코스닥')
        } else if (overView.corp_cls === 'N') {
            set법인구분('코넥스')
        } else {
            set법인구분('장외시장 or 상장폐지')
        }

        if (overView.est_dt !== '') {
            set설립일(() => overView.est_dt.slice(0, 4) + '-' + overView.est_dt.slice(4,6) + '-' + overView.est_dt.slice(6) )
        }

        if (overView.jurir_no !== '' && overView.jurir_no.length === 13) {
            set법인등록번호(() => overView.jurir_no.slice(0, 6) + '-' + overView.jurir_no.slice(6))
        } else {
            set법인등록번호(()=>overView.jurir_no)
        }

        if (overView.bizr_no !== '' && overView.bizr_no.length === 10) {
            set사업자등록번호(() => overView.bizr_no.slice(0, 3) + '-' + overView.bizr_no.slice(3, 5) + '-' + overView.bizr_no.slice(5))
        } else {
            set사업자등록번호(()=>overView.bizr_no)
        }

        if (overView.hm_url === '' || overView.hm_url === '_') {
            
            setLinkActivated(() => true)
        } else {
            if (overView.hm_url.slice(0, 5) === 'https') {
                set메인페이지(() => overView.hm_url)
                setLinkActivated(() => false)
            } else {
                set메인페이지(() => 'https:/' + overView.hm_url)
                setLinkActivated(() => false)
            }
        }

        if (overView.ir_url !== '') {
            if (overView.ir_url.slice(0, 5) === 'https') {
                setIR페이지(() => overView.ir_url)
                setIrLinkActivated(() => false)
            } else {
                setIR페이지(() => 'https:/' + overView.ir_url)
                setIrLinkActivated(() => false)
            }
        } else {
            setIrLinkActivated(() => true)
        }


    }, [overView])
    return (
        <>
            <Container>
                <Row>
                    <Col>
                        <Card border="light">
                            <Card.Body>
                                <Card.Title>{overView.stock_name}</Card.Title>
                                <Card.Subtitle className="mb-3 text-muted">{overView.corp_name_eng}</Card.Subtitle>
                                <Button variant="outline-dark" onClick={() => { window.open(메인페이지, '_blank') }} disabled={linkActivated}>웹사이트</Button>
                                &nbsp; &nbsp; &nbsp;
                                <Button variant="outline-dark" onClick={() => { window.open(IR페이지, '_blank') }} disabled={irLinkActivated}>IR PAGE</Button>
                            </Card.Body>

                            <Container>
                                <Row>
                                    <Col lg='6'>
                                        <Card>
                                            <ListGroup className="list-group-flush">
                                                <ListGroup.Item><small className="text-muted">대표자명:</small> {overView.ceo_nm}</ListGroup.Item>
                                                <ListGroup.Item><small className="text-muted">주소:</small> {overView.adres}</ListGroup.Item>
                                                <ListGroup.Item><small className="text-muted">법인구분:</small> {법인구분}</ListGroup.Item>
                                                <ListGroup.Item><small className="text-muted">전화번호:</small> {overView.phn_no}</ListGroup.Item>
                                                <ListGroup.Item><small className="text-muted">설립일:</small> {설립일}</ListGroup.Item>
                                                <ListGroup.Item><small className="text-muted">결산월:</small> {overView.acc_mt}월</ListGroup.Item>
                                            </ListGroup>
                                        </Card>
                                    </Col>
                                    <Col lg='6'>
                                        <Card>
                                            <ListGroup variant="flush">
                                                <ListGroup.Item><small className="text-muted">종목코드:</small> {overView.stock_code}</ListGroup.Item>
                                                <ListGroup.Item><small className="text-muted">고유번호:</small> {overView.corp_code}</ListGroup.Item>
                                                <ListGroup.Item><small className="text-muted">법인등록번호:</small> {법인등록번호}</ListGroup.Item>
                                                <ListGroup.Item><small className="text-muted">사업자등록번호:</small> {사업자등록번호}</ListGroup.Item>
                                                <ListGroup.Item><small className="text-muted">팩스번호:</small> {overView.fax_no}</ListGroup.Item>
                                                <ListGroup.Item><small className="text-muted">업종코드:</small> {overView.induty_code}</ListGroup.Item>
                                            </ListGroup>
                                        </Card>
                                    </Col>
                                </Row>
                                <br />
                            </Container>


                        </Card>
                    </Col>
                </Row>
            </Container>

        </>
    )
}