import React, { useState } from "react";
import { ResponsiveLine } from '@nivo/line'
import { useSelector } from "react-redux";
import { useEffect } from "react";
import Button from 'react-bootstrap/Button';
import Accordion from 'react-bootstrap/Accordion';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Card from 'react-bootstrap/Card';


export default function BuffettGraph() {
    //Url state가져오는 코드
    const localUrl = useSelector((state) => state.localUrl)
    const itemUrlKR = '/econ/buffett_index/kr'
    const itemUrlUS = '/econ/buffett_index/us'

    //fetch로 가져온 data 임시저장
    const [data, setData] = useState([])

    //시작과 끝 날짜 저장
    const [startDate, setStartDate] = useState('')
    const [endDate, setEndDate] = useState('')

    //data 테스트 버튼 함수
    async function testData() {
        console.log(data)
    }

    try {
        //처음 렌더링시 fetch 함수
        useEffect(() => {
            async function getData() {
                let fetchKR = await fetch(localUrl + itemUrlKR)
                    .then((res) => res.json())

                let fetchUS = await fetch(localUrl + itemUrlUS)
                    .then((res) => res.json())

                //시작과 끝 날짜 추출
                setStartDate(fetchKR[0]['data'][0]['x'])
                setEndDate(fetchUS[0]['data'][fetchUS[0]['data'].length - 1]['x'])

                //색상 변경 코드
                fetchKR[0]['color'] = 'hsl(158, 70%, 50%)'
                fetchUS[0]['color'] = 'hsl(20, 70%, 50%)'

                const result = [
                    fetchKR[0],
                    fetchUS[0],
                ]

                setData(result)
            };
            getData()
        }, [])

    } catch (err) {
        console.log('Vix 지수(3개월) 렌더링 오류')
    }

    const BuffettGraph

        = ({ data }) => (
            <ResponsiveLine
                data={data}
                margin={{ top: 60, right: 110, bottom: 100, left: 60 }}
                xScale={{ type: 'point' }}
                yScale={{
                    type: 'linear',
                    min: 'auto',
                    max: 'auto',
                    stacked: false,
                    reverse: false
                }}
                yFormat=" >-.2f"
                axisTop={null}
                axisRight={null}
                axisBottom={{
                    orient: 'bottom',
                    tickSize: 5,
                    tickPadding: 5,
                    tickRotation: -80,
                    legend: '',
                    legendOffset: 50,
                    legendPosition: 'middle'
                }}
                axisLeft={{
                    orient: 'left',
                    tickSize: 5,
                    tickPadding: 5,
                    tickRotation: 0,
                    legend: '',
                    legendOffset: -40,
                    legendPosition: 'middle'
                }}
                colors={{ scheme: 'category10' }}
                lineWidth={4}
                pointSize={6}
                pointColor={{ from: 'color', modifiers: [] }}
                pointBorderWidth={2}
                pointBorderColor={{ from: 'serieColor' }}
                pointLabelYOffset={-12}
                enableSlices="x"
                useMesh={true}
                legends={[
                    {
                        anchor: 'bottom-right',
                        direction: 'column',
                        justify: false,
                        translateX: 100,
                        translateY: 0,
                        itemsSpacing: 0,
                        itemDirection: 'left-to-right',
                        itemWidth: 80,
                        itemHeight: 20,
                        itemOpacity: 0.75,
                        symbolSize: 12,
                        symbolShape: 'circle',
                        symbolBorderColor: 'rgba(0, 0, 0, .5)',
                        effects: [
                            {
                                on: 'hover',
                                style: {
                                    itemBackground: 'rgba(0, 0, 0, .03)',
                                    itemOpacity: 1
                                }
                            }
                        ]
                    }
                ]}
            />
        )
    return (
        <>

            <Container>
                <Card border="light" >
                    <Card.Body>
                        <Card.Title className="text-center">한국과 미국의 버핏지수 [10년]</Card.Title>
                        <Card.Text className="text-center">
                            {startDate} ~ {endDate}
                        </Card.Text>
                        {/* <Button variant="primary" onClick={testData}>Primary</Button>{' '} */}
                        <div style={{ width: 'auto', height: '400px', margin: '0 auto' }}>
                            <BuffettGraph data={data} />
                        </div>
                        <Row>
                            <Col></Col>
                            <Col lg="8">
                                <Accordion>
                                    <Accordion.Item eventKey="0">
                                        <Accordion.Header>버핏지수</Accordion.Header>
                                        <Accordion.Body>
                                            버핏 지수는 국내총생산(GDP) 대비 시가총액의 비율이며 해당 국가의 시가총액/국내총생산(GDP)*100으로 계산한다. <br />
                                            국내총생산(GDP, gross domestic product)은 한 나라에서 생산된 모든 최종생산물의 가치이며, 시가총액은 모든 상장주식을 시가로 평가한 총액이다. <br />
                                            버핏 지수의 평가 기준은 각 국가의 상황에 따라 다르기는 하지만, 일반적으로 버핏 지수가 80~100% 사이이면 증시가 적정구간에 있다고 보며, <br />
                                            80% 보다 낮으면 저평가된 것으로, 100% 보다 높으면 고평가되어 있어 증시에 거품(bubble)이 있다고 판단한다.
                                        </Accordion.Body>
                                    </Accordion.Item>
                                    <Accordion.Item eventKey="1">
                                        <Accordion.Header>개발자의 첨언</Accordion.Header>
                                        <Accordion.Body>
                                            일률적으로 버핏지수가 100% 보다 높으면 고평가 되어 있다고 생각하기 어렵습니다. <br />
                                            위의 기준은 20년도 더 된 기준이며 최근 주식시장으로 유입되는 금액이 커짐에 따라서 지속적으로 지수가 높아지고 있습니다.<br />
                                            필자는 경기침체라고 생각되는 시기에 GDP 대비 시가총액의 변화율을 관찰하기 위해서 씁니다.<br />
                                            <br />
                                            이 지수에 쓰인 GDP 데이터는 명목 GDP로 계절조정이 되어 있습니다. <br />
                                            실질 GDP가 아닌 명목 GDP를 쓴 이유는 시가총액 데이터는 인플레이션에 노출이 되어 있으므로, GDP 또한 인플레이션에 노출이 되어있어야 한다고 생각합니다.<br />
                                            또한, 다른 버핏지수와는 다르게 전년도 GDP가 아닌 최대한 같은 분기, 늦어도 그 전분기 GDP와 시가총액을 비교하였습니다.<br />
                                            미국의 경우 한국보다 장외 시장규모도 큰 편임으로 장외 시장의 시가총액이 고려된 Wilshire 5000 Full Cap Price Index를 사용하였습니다.
                                        </Accordion.Body>
                                    </Accordion.Item>
                                    <Accordion.Item eventKey="2">
                                        <Accordion.Header>자료 출처</Accordion.Header>
                                        <Accordion.Body>
                                            세인트루이스 연반준비은행(FRED, Federal Reserve Economic Data) <br />
                                            Gross Domestic Product (GDP)<br />
                                            Wilshire 5000 Full Cap Price Index (WILL5000PRFC)<br />
                                            <br />
                                            한국은행 경제통계시스템 <br />
                                            경제활동별 GDP 및 GNI(계절조정, 명목, 분기)[200Y003] 국내총생산[1400] <br />
                                            주식시장[901Y014] KOSPI_시가총액[1040000], KOSDAQ_시가총액[2040000]
                                        </Accordion.Body>
                                    </Accordion.Item>
                                </Accordion>
                            </Col>
                            <Col></Col>
                        </Row>
                    </Card.Body>
                </Card>

            </Container>
        </>
    )
}