import React, { useState, useEffect } from 'react';
import Button from 'react-bootstrap/Button';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Form from 'react-bootstrap/Form';
import corpNameArrey from './datas/corpNameArrey.json'
import Card from 'react-bootstrap/Card';
import Toast from 'react-bootstrap/Toast';
import ToastContainer from 'react-bootstrap/ToastContainer';



import { corpName_input } from '../store/corpSlice/corpNameSlice'
import { overView_input } from '../store/corpSlice/overViewSlice';
import { useSelector, useDispatch } from 'react-redux'



function MainSearch() {
    //리덕스
    const dispatch = useDispatch()
    const overView = useSelector((state: any) => state.overView)
    const corpName = useSelector((state: any)=> state.corpName.value)

    //URLs
    const localUrl = useSelector((state: any) => state.localUrl)
    const backEndUrl = useSelector((state: any) => state.backEndUrl)

    //api item url
    const itemUrl = '/overview/'

    //검색창에 타입한거 저장  
    const [input_name, setInput_name] = useState("");
    const input_onChange = (text: any) => {
        setInput_name(text.target.value.trim().toUpperCase())
        setIsHaveInputValue(true)
    }

    //엔터로 제출하는 함수
    const submitEnter = (e: any) => {
        if (e.key === 'Enter') {
            for (let i in corpNameArrey) {
                if (corpNameArrey[i] === input_name) {
                    dispatch(corpName_input(input_name))
                    button_onClick();
                    break;
                }
            }

        }
    }

    //자동완성 파트 State모음
    const [isHaveInputValue, setIsHaveInputValue] = useState<boolean>(false)
    const [dropDownList, setDropDownList] = useState(corpNameArrey)
    const [dropDownItemIndex, setDropDownItemIndex] = useState(-1)
    const [showToast, setShowToast] = useState<boolean>(false)
    const [showActivated, setShowActivated] = useState<boolean>(false)
    const [errToast, setErrToast] = useState<boolean>(false)
    const [현재회사, set현재회사] = useState<boolean>(false)
    const [warningToast, setWarningToast] = useState<boolean>(false)

    //자동완성 검색창
    function showDropDownList() {
        if (input_name === '') {
            setIsHaveInputValue(false)
            setDropDownList([])
        } else {
            const choosenTextList = corpNameArrey.filter(textItem =>
                textItem.includes(input_name)
            )
            setDropDownList(choosenTextList)
        }
    }

    const clickDropDownItem = clickedItem => {
        setInput_name(clickedItem)
        setIsHaveInputValue(false)
    }

    function handleDropDownKey(event) {
        if (isHaveInputValue) {
            if (
                event.key === 'ArrowDown' &&
                dropDownList.length - 1 > dropDownItemIndex
            ) {
                setDropDownItemIndex(dropDownItemIndex + 1)
            }

            if (event.key === 'ArrowUp' && dropDownItemIndex >= 0)
                setDropDownItemIndex(dropDownItemIndex - 1)
            if (event.key === 'Enter' && dropDownItemIndex >= 0) {
                // clickDropDownItem(dropDownList[dropDownItemIndex])
                setDropDownItemIndex(-1)
            }
        }
    }

    useEffect(showDropDownList, [input_name])


    //클릭하면 회사이름 api 전달
    async function button_onClick() {


        try {
            let result = await fetch(localUrl + itemUrl + input_name).then((res) => res.json())
            dispatch(overView_input(result))
            setShowToast(()=>(true))
            set현재회사(()=>true)
            setWarningToast(false)
            setShowActivated(false)
            if(result['corp_cls'] === 'E'){
                setTimeout(()=>setWarningToast(true),800)
            } else {
                setTimeout(()=>setShowActivated(true),400)
            }

        } catch (e) {
            setErrToast(()=>(true))
            console.log(`[${input_name}] 해당 회사를 못 찾았습니다.`, e)
        }

    }

    return (
        <>
            <Container fluid='md'>
                <br /><br /><br /><br />
                <Row>
                    <Col></Col>
                    <Col lg='6'><h1>어떤 회사를 찾으세요?</h1></Col>
                    <Col></Col>
                    <Col></Col>
                </Row>
                <br />
                <Row>
                    <Col></Col>
                    <Col lg='6'>
                        <Form.Group className="lg-3" controlId="formBasicEmail">
                            {/* <Form.Group className="mb-3" controlId="formBasicEmail" isHaveInputValue={isHaveInputValue}> */}
                            {/* <Form.Group className="mb-3" controlId="formBasicEmail" ishaveinputvalue={isHaveInputValue}> */}
                            {/* 있으면 에러 뜨고 없어도 작동을 함  */}
                            <Form.Control type="text" placeholder="기업명을 입력해주세요."
                                value={input_name}
                                onChange={input_onChange}
                                onKeyDown={submitEnter}
                                onKeyUp={handleDropDownKey}
                                size='lg' />
                            <Form.Text className="text-muted">
                            </Form.Text>
                            <>
                                {isHaveInputValue && (
                                    <ul>
                                        {dropDownList.length === 0 && (
                                            <li>해당하는 회사가 없습니다</li>
                                        )}
                                        {dropDownList.map((dropDownItem, dropDownIndex) => {
                                            return (
                                                <li
                                                    key={dropDownIndex}
                                                    onClick={() => clickDropDownItem(dropDownItem)}
                                                    onMouseOver={() => setDropDownItemIndex(dropDownIndex)}
                                                    className={
                                                        dropDownItemIndex === dropDownIndex ? 'selected' : ''
                                                    }
                                                >
                                                    {dropDownItem}
                                                </li>
                                            )
                                        })}
                                    </ul>
                                )}
                            </>
                        </Form.Group>
                    </Col>
                    <Col></Col>
                    <Col></Col>
                </Row>
                <Row>
                    <Col></Col>
                    <Col></Col>
                    <Col></Col>
                </Row>
                <br /><br /><br /><br />
                <ToastContainer position='bottom-end' className="p-3">

                    <Toast show={showToast} onClose={() => setShowToast(false)} delay={5000} autohide>
                        <Toast.Header>
                            <strong className="me-auto" style={{ color: 'indigo'}}>Succeed</strong>
                        </Toast.Header>
                        <Toast.Body><strong style={{ color: 'indigo'}}>{corpName}</strong> 정보를 가져왔습니다 </Toast.Body>
                    </Toast>

                    <Toast show={showActivated} onClose={() => setShowActivated(false)} delay={5000} autohide>
                        <Toast.Header>
                            <strong className="me-auto" style={{ color: 'seagreen'}}>Activated</strong>
                        </Toast.Header>
                        <Toast.Body>메뉴가 <strong style={{ color: 'seagreen'}}>활성화</strong> 되었습니다 </Toast.Body>
                    </Toast>

                    <Toast show={errToast} onClose={() => setErrToast(false)} delay={5000} autohide bg='danger'>
                        <Toast.Header>
                            <strong className="me-auto">Failed</strong>
                        </Toast.Header>
                        <Toast.Body style={{ color: 'white'}}><strong>{corpName}</strong> 정보를 불러오는데 실패했습니다 </Toast.Body>
                    </Toast>

                    <Toast show={warningToast} onClose={() => setWarningToast(false)} delay={10000} autohide bg='warning'>
                        <Toast.Header>
                            <strong className="me-auto">Warning</strong>
                        </Toast.Header>
                        <Toast.Body> <strong>상장폐지</strong> 또는 <strong>비상장</strong> 회사입니다 </Toast.Body>
                    </Toast>

                    </ToastContainer>

                    <ToastContainer position='bottom-start' className="p-3">

                    <Toast show={현재회사} onClick={() => set현재회사(!현재회사)} bg='secondary'>
                        <Toast.Header>
                            <strong className="me-auto">현재 입력된 회사</strong>
                        </Toast.Header>
                        <Toast.Body style={{ color: 'white'}}><strong>{corpName}</strong>  </Toast.Body>
                    </Toast>

                </ToastContainer>
                
            </Container>
        </>
    )
}


export default MainSearch;