import React from "react";
import Button from 'react-bootstrap/Button';
import { useSelector, useDispatch } from "react-redux";
import Alert from 'react-bootstrap/Alert';
import { Container } from "react-bootstrap";
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import { useState, useEffect } from "react";
import Card from 'react-bootstrap/Card';
import ListGroup from 'react-bootstrap/ListGroup';
import Placeholder from 'react-bootstrap/Placeholder';
import Spinner from 'react-bootstrap/Spinner';
import { srimCorpValue_input, srimCorpValue_partInput, srimCorpValue_reset } from "../../store/srimSlice/srimCorpValueSlice";
import { fetchSrim_input } from "../../store/srimSlice/fetchSrimSlice";
import { srimStockPrice_input, srimStockPrice_partInput, srimStockPrice_reset } from "../../store/srimSlice/srimStockPriceSlice";
import Table from 'react-bootstrap/Table';
import OverlayTrigger from 'react-bootstrap/OverlayTrigger';
import Tooltip from 'react-bootstrap/Tooltip';
import SrimValueGraph from '../graphs/srimValueGraph'
import SrimStockGraph from '../graphs/srimStockGraph.js'
import ToastContainer from 'react-bootstrap/ToastContainer';
import Toast from 'react-bootstrap/Toast';




export default function Srim() {
    const dispatch = useDispatch();
    const overView = useSelector((state: any) => state.overView)
    const localUrl = useSelector((state: any) => state.localUrl)
    const fetchSrim = useSelector((state: any) => state.fetchSrim)
    const srimCorpValue = useSelector((state: any) => state.srimCorpValue)
    const srimStockPrice = useSelector((state: any) => state.srimStockPrice)

    const itemUrl = '/company/srim/'

    const [alertShow, setAlertShow] = useState<boolean>(true)
    const [loading, setLoading] = useState<boolean>(false)
    const [우선주총합, set우선주총합] = useState('0')
    const [우선주이름, set우선주이름] = useState(['우선주 없음'])
    const [errToast, setErrToast] = useState<boolean>(false)
    const [noRoeToast, setNoRoeToast] = useState<boolean>(false)
    const [lowRoeToast, setLowRoeToast] = useState<boolean>(false)
    const [highPriceToast, setHighPriceToast] = useState<boolean>(false)
    const [lowPriceToast, setLowPriceToast] = useState<boolean>(false)


    function getCorpValue유지(자기자본: number, ROE: number, 회사채: number) {
        //기업가치 = 자기자본(지배주주자본총계) + 초과이익 현재가치(예상 ROE, 요구수익률(회사채),자기자본(지배주주자본총계)) + 초과이익유지율
        if (ROE < 회사채) {
            setLowRoeToast(() => true)
            return 0
        }
        const 기업가치 = Math.round(자기자본 + (자기자본 * (ROE - 회사채)) / 회사채)
        return 기업가치
    }

    function getCorpValue감소(자기자본: number, ROE: number, 회사채: number, 유지율: number) {
        //기업가치 = 자기자본(지배주주자본총계) + 초과이익 현재가치(예상 ROE, 요구수익률(회사채),자기자본(지배주주자본총계)) + 초과이익유지율
        if (ROE < 회사채) {
            return 0
        }
        const 기업가치 = Math.round(자기자본 + ((자기자본 * (ROE - 회사채)) * (유지율 / (1 + 회사채 - 유지율))))
        return 기업가치
    }

    function getStockPrice(기업가치: number, 계산용주식수: number, 우선주시총합: number) { //적정주가 구하는 공식
        if (기업가치 === 0) {
            return 0
        }
        const 적정주가 = Math.round((기업가치 - 우선주시총합) / 계산용주식수)
        return 적정주가
    }

    function numWithComma(num: number) {
        return num.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',')
    }

    function numWithComma억(num: number) {
        return Math.round(num / 100000000).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',')

    }

    //툴팁
    const 우선주Tooltip = (props) => (
        <Tooltip id="button-tooltip" {...props}>
            {우선주이름}
        </Tooltip>
    );
    const 자본총계Tooltip = (props) => (
        <Tooltip id="button-tooltip" {...props}>
            지배주주지분
        </Tooltip>
    );

    //검색하면 재실행 useEffect
    useEffect(() => {
        setAlertShow(() => true)
        setLoading(() => false)
        set우선주총합(() => '0')
        set우선주이름(() => ['우선주 없음'])
        dispatch(srimCorpValue_reset())
        dispatch(srimStockPrice_reset())
    }, [overView])

    //버튼 클릭시 실행
    async function button_onClick() {
        setLoading(true)

        try {
            const fetchRes = await fetch(localUrl + itemUrl + overView.corp_code + '/' + overView.stock_code).then((res) => res.json())
            dispatch(fetchSrim_input(fetchRes))

            if (fetchRes['주식정보']['시세종가'] === '0') {
                setErrToast(() => true)
            }

            let 적정주가모음 = []
            let 기업가치모음 = []
            let 자기자본모음 = []
            let ROEs = []
            const 유지율90 = 0.9
            const 유지율80 = 0.8
            const 회사채 = Number((fetchRes['회사채금리']['DATA_VALUE'] / 100).toFixed(5))
            let 우선주시총합 = 0
            let 우선주명 = []
            let 계산용주식수 = Number(fetchRes['주식정보']['유통주식수'].replace(/,/g, ''))

            //자기자본과 ROE를 순서대로 저장
            for (let i in fetchRes['재무정보']) {
                자기자본모음.push(Number(fetchRes['재무정보'][i]['주주지분'].replace(/,/g, '')) * 100000000)  //억원
                ROEs.push(Number(fetchRes['재무정보'][i]['ROE']) / 100)
            }

            //ROE가 -가 되면 기업가치가 음수가 됨
            //회사채보다 ROE가 낮으면 결과값이 제대로 안 나옴

            //2023년도 ROE가 없다면, 기업가치 구하기
            if (ROEs[5] === 0) { //ROE 컨센서스가 없다면 가중평균으로 ROE를 구해서 계산
                let ROE2023 = (ROEs[4] * 3 + ROEs[3] * 2 + ROEs[2]) / 6
                const 기업가치2023유지 = getCorpValue유지(자기자본모음[4], ROE2023, 회사채)
                const 기업가치2023감소10 = getCorpValue감소(자기자본모음[4], ROE2023, 회사채, 유지율90)
                const 기업가치2023감소20 = getCorpValue감소(자기자본모음[4], ROE2023, 회사채, 유지율80)
                기업가치모음.push({
                    '연도': fetchRes['재무정보'][5]['TIME'].slice(0, 4),
                    '기업가치유지': 기업가치2023유지,
                    '기업가치감소10': 기업가치2023감소10,
                    '기업가치감소20': 기업가치2023감소20,
                })
                setNoRoeToast(() => true)
                dispatch(srimCorpValue_partInput(기업가치모음[0]))
            } else { //컨센서스가 있다면 컨센서스로 계산
                for (let i = 4; i < 7; i++) {
                    const 기업가치유지 = getCorpValue유지(자기자본모음[i], ROEs[i + 1], 회사채)
                    const 기업가치감소10 = getCorpValue감소(자기자본모음[i], ROEs[i + 1], 회사채, 유지율90)
                    const 기업가치감소20 = getCorpValue감소(자기자본모음[i], ROEs[i + 1], 회사채, 유지율80)

                    기업가치모음.push({
                        '연도': fetchRes['재무정보'][i + 1]['TIME'].slice(0, 4),
                        '기업가치유지': 기업가치유지,
                        '기업가치감소10': 기업가치감소10,
                        '기업가치감소20': 기업가치감소20,
                    })
                }
                dispatch(srimCorpValue_input(기업가치모음))
            }

            if (Array.isArray(fetchRes['주식정보']['우선주시총'])) { // 우선주의 정보가 있다면 우선주의 시가총액을 고려

                for (let i in fetchRes['주식정보']['우선주시총']) {
                    우선주시총합 = 우선주시총합 + Number(fetchRes['주식정보']['우선주시총'][i]['우선주시가총액'])
                    우선주명.push(fetchRes['주식정보']['우선주시총'][i]['우선주명'] + ', ')
                }

                for (let i in 기업가치모음) {
                    적정주가모음.push({
                        '연도': 기업가치모음[i]['연도'],
                        '적정주가유지': getStockPrice(기업가치모음[i]['기업가치유지'], 계산용주식수, 우선주시총합),
                        '적정주가감소10': getStockPrice(기업가치모음[i]['기업가치감소10'], 계산용주식수, 우선주시총합),
                        '적정주가감소20': getStockPrice(기업가치모음[i]['기업가치감소20'], 계산용주식수, 우선주시총합),
                    })
                }

                //우선주 정보란에 정보 입력
                set우선주총합(() => numWithComma억(우선주시총합))
                set우선주이름(() => 우선주명)

                if (적정주가모음.length === 1) { // 적정주가 가 2023년 밖에 없다면 첫번쨰 배열만 부분교체
                    dispatch(srimStockPrice_partInput(적정주가모음[0]))
                } else {
                    dispatch(srimStockPrice_input(적정주가모음))
                }
            } else {
                for (let i in 기업가치모음) { //우선주가 없다면 그냥 적정주가 계산
                    적정주가모음.push({
                        '연도': 기업가치모음[i]['연도'],
                        '적정주가유지': getStockPrice(기업가치모음[i]['기업가치유지'], 계산용주식수, 우선주시총합),
                        '적정주가감소10': getStockPrice(기업가치모음[i]['기업가치감소10'], 계산용주식수, 우선주시총합),
                        '적정주가감소20': getStockPrice(기업가치모음[i]['기업가치감소20'], 계산용주식수, 우선주시총합),
                    })
                }
                if (적정주가모음.length === 1) { // 2023년 밖에 없다면 첫번째 배열만 교체
                    dispatch(srimStockPrice_partInput(적정주가모음[0]))
                } else {
                    dispatch(srimStockPrice_input(적정주가모음))
                }
            }
            setAlertShow(() => false)

        } catch (err) {
            setErrToast(() => true)
            console.log(err)
        }

    }


    return (
        <>
            <Container>
                <Alert variant="warning" show={alertShow}>
                    <Alert.Heading>유의 사항</Alert.Heading>
                    <p>
                        본 메뉴는 사경인 회계사님의 저서인 <strong>[재무제표 모르면 주식투자 절대로 하지 마라]</strong>를 바탕으로 만들어졌습니다.
                    </p>
                    <p>
                        <strong>S-RIM</strong>은 자기자본과 ROE, 요구수익률를 활용하여 적정주가를 알아내기 위한 공식입니다.
                    </p>
                    <p>
                        자기자본은 지배주주지분 값을 사용하였습니다. <br /> 
                        ROE는 최대한 컨센서스를 이용하였고, 만약 컨센서스 ROE가 없다면, 책에서 소개된 가중평균 ROE 값으로 대체하였습니다.<br />
                        우선주가 있는 종목일 경우 모든 우선주의 시가총액을 이용하였습니다.<br />
                        요구수익률(할인율)은 BBB- 3년 회사채 금리를 이용하였습니다.<br/>
                        계산에 활용된 주식수는 발행보통주식수에서 자사주를 제한 유통주식수를 활용하였습니다.<br/>
                    </p>
                    <p>
                        <strong>ROE가 최소 투자 등급인 BBB- 회사채보다 낮은 경우 투자할 이유가 없다고 가정하여 결과가 나오지 않습니다.</strong>
                    </p>
                    <p>
                        <strong>(경고) 해당 사이트는 참고 사항일 뿐, 모든 투자에 대한 책임은 투자자 본인에게 있습니다.</strong>
                    </p>
                    <hr />
                    <div className="d-flex justify-content-start">
                        {loading
                            ? <Button variant="warning" disabled>
                                <Spinner
                                    as="span"
                                    animation="border"
                                    role="status"
                                    size='sm'
                                    aria-hidden="true"
                                />
                                <span className="visually-hidden">Loading...</span>
                            </Button>
                            :
                            <Button onClick={button_onClick} variant="outline-warning">
                                확인했습니다
                            </Button>
                        }
                    </div>
                </Alert>

                {!alertShow &&
                    <Row>
                        <Col>
                            <Card border="light">
                                <Card.Body>
                                    <Card.Title>{overView.stock_name}</Card.Title>
                                    <Card.Subtitle className="mb-3 text-muted">{overView.corp_name_eng}</Card.Subtitle>
                                </Card.Body>

                                <Container>
                                    <Row>
                                        <Col>
                                            <Card>
                                                <Card.Header>종목 정보</Card.Header>
                                                <ListGroup variant="flush">
                                                    <ListGroup.Item><small className="text-muted">유통주식수: </small>{fetchSrim['주식정보']['유통주식수']}<small>주</small></ListGroup.Item>
                                                    <ListGroup.Item>
                                                        <OverlayTrigger
                                                            placement="bottom"
                                                            overlay={우선주Tooltip}
                                                        ><small className="text-muted">우선주 시가총액: </small></OverlayTrigger>{우선주총합}<small>억원</small></ListGroup.Item>
                                                </ListGroup>
                                            </Card>
                                        </Col>

                                        <Col>
                                            <Card>
                                                <Card.Header>주가 정보</Card.Header>
                                                <ListGroup variant="flush">
                                                    <ListGroup.Item><small className="text-muted">기준일: </small>{fetchSrim['주식정보']['시세기준일'].slice(0, 4)}-{fetchSrim['주식정보']['시세기준일'].slice(4, 6)}-{fetchSrim['주식정보']['시세기준일'].slice(6)}</ListGroup.Item>
                                                    <ListGroup.Item><small className="text-muted">종가: </small>{numWithComma(fetchSrim['주식정보']['시세종가'])}<small>원</small></ListGroup.Item>
                                                </ListGroup>
                                            </Card>
                                        </Col>

                                        <Col>
                                            <Card>
                                                <Card.Header>회사채 (<small>3년</small>, <small>BBB-</small>)</Card.Header>
                                                <ListGroup variant="flush">
                                                    <ListGroup.Item><small className="text-muted">기준일: </small>{fetchSrim['회사채금리']['TIME'].slice(0, 4)}-{fetchSrim['회사채금리']['TIME'].slice(4, 6)}-{fetchSrim['회사채금리']['TIME'].slice(6)}</ListGroup.Item>
                                                    <ListGroup.Item><small className="text-muted">금리: </small>{fetchSrim['회사채금리']['DATA_VALUE']}<small>%</small></ListGroup.Item>
                                                </ListGroup>
                                            </Card>
                                        </Col>

                                    </Row>
                                </Container>
                                <br />
                                <Container>
                                    <Table striped bordered hover size="sm">
                                        <thead>
                                            <tr>
                                                <th>#</th>
                                                <th>{fetchSrim['재무정보'][0]['TIME']}</th>
                                                <th>{fetchSrim['재무정보'][1]['TIME']}</th>
                                                <th>{fetchSrim['재무정보'][2]['TIME']}</th>
                                                <th>{fetchSrim['재무정보'][3]['TIME']}</th>
                                                <th>{fetchSrim['재무정보'][4]['TIME']}</th>
                                                <th>{fetchSrim['재무정보'][5]['TIME']}<small>(E)</small></th>
                                                <th>{fetchSrim['재무정보'][6]['TIME']}<small>(E)</small></th>
                                                <th>{fetchSrim['재무정보'][7]['TIME']}<small>(E)</small></th>
                                            </tr>
                                        </thead>
                                        <tbody>

                                            <tr>
                                                <td><OverlayTrigger
                                                    placement="left"
                                                    overlay={자본총계Tooltip}
                                                ><small>자본총계</small></OverlayTrigger></td>
                                                <td>{fetchSrim['재무정보'][0]['주주지분']}<small>억원</small></td>
                                                <td>{fetchSrim['재무정보'][1]['주주지분']}<small>억원</small></td>
                                                <td>{fetchSrim['재무정보'][2]['주주지분']}<small>억원</small></td>
                                                <td>{fetchSrim['재무정보'][3]['주주지분']}<small>억원</small></td>
                                                <td>{fetchSrim['재무정보'][4]['주주지분']}<small>억원</small></td>
                                                <td>{fetchSrim['재무정보'][5]['주주지분']}<small>억원</small></td>
                                                <td>{fetchSrim['재무정보'][6]['주주지분']}<small>억원</small></td>
                                                <td>{fetchSrim['재무정보'][7]['주주지분']}<small>억원</small></td>
                                            </tr>
                                            <tr>
                                                <td><small>ROE</small></td>
                                                <td>{fetchSrim['재무정보'][0]['ROE']}<small>%</small></td>
                                                <td>{fetchSrim['재무정보'][1]['ROE']}<small>%</small></td>
                                                <td>{fetchSrim['재무정보'][2]['ROE']}<small>%</small></td>
                                                <td>{fetchSrim['재무정보'][3]['ROE']}<small>%</small></td>
                                                <td>{fetchSrim['재무정보'][4]['ROE']}<small>%</small></td>
                                                <td>{fetchSrim['재무정보'][5]['ROE']}<small>%</small></td>
                                                <td>{fetchSrim['재무정보'][6]['ROE']}<small>%</small></td>
                                                <td>{fetchSrim['재무정보'][7]['ROE']}<small>%</small></td>
                                            </tr>

                                        </tbody>
                                    </Table>
                                </Container>
                            </Card>
                            <SrimStockGraph></SrimStockGraph>
                            <SrimValueGraph></SrimValueGraph>
                        </Col>
                    </Row>
                }

                {alertShow &&

                    < Row >
                        <Col>
                            <Card border="light">
                                <Card.Body>
                                    <Card.Title>{overView.stock_name}</Card.Title>
                                    <Card.Subtitle className="mb-3 text-muted">{overView.corp_name_eng}</Card.Subtitle>  
                                    <Placeholder as={Card.Title} animation="glow">
                                        <Placeholder xs={6} />
                                    </Placeholder>
                                    <Placeholder as={Card.Text} animation="glow">
                                        <Placeholder xs={7} /> <Placeholder xs={4} /> <Placeholder xs={4} />{' '}
                                        <Placeholder xs={6} /> <Placeholder xs={8} />
                                    </Placeholder>
                                </Card.Body>
                            </Card>
                        </Col>
                    </Row>
                }
            </Container >

            <ToastContainer className="p-3" position="bottom-end">

                <Toast bg='warning' show={noRoeToast} onClose={() => setNoRoeToast(false)} delay={5000} autohide>
                    <Toast.Header>
                        <strong className="me-auto">ROE 컨센서스 없음</strong>
                    </Toast.Header>
                    <Toast.Body>가중평균 ROE로 <strong>대체</strong>됩니다.</Toast.Body>
                </Toast>

                <Toast bg='warning' show={lowRoeToast} onClose={() => setLowRoeToast(false)} delay={5000} autohide>
                    <Toast.Header>
                        <strong className="me-auto">ROE가 금리보다 낮음</strong>
                    </Toast.Header>
                    <Toast.Body>ROE가 금리보다 낮은 <strong>적정주가</strong>와 <strong>기업가치</strong>는 <strong>제외</strong>됩니다.</Toast.Body>
                </Toast>

                <Toast bg='danger' show={errToast} onClick={() => setErrToast(false)}>
                    <Toast.Header>
                        <strong className="me-auto">Faild</strong>
                    </Toast.Header>
                    <Toast.Body style={{ color: 'white' }}>문제가 생겼습니다. 다시 시도해 주세요.</Toast.Body>
                </Toast>

            </ToastContainer>

            <ToastContainer className="p-3" position="bottom-start">

                <Toast show={highPriceToast} onClose={() => setHighPriceToast(false)} delay={5000} autohide>
                    <Toast.Header style={{ color: 'darkorange' }}>
                        <strong className="me-auto">고평가</strong>
                    </Toast.Header>
                    <Toast.Body>현재 주가가 적정가격보다 높습니다.</Toast.Body>
                </Toast>

                <Toast show={lowPriceToast} onClose={() => setLowPriceToast(false)} delay={5000} autohide>
                    <Toast.Header style={{ color: 'forestgreen' }}>
                        <strong className="me-auto">저평가</strong>
                    </Toast.Header>
                    <Toast.Body>현재 주가가 적정가격보다 저렴합니다.</Toast.Body>
                </Toast>

            </ToastContainer>
        </>
    )

}