import { createSlice } from '@reduxjs/toolkit'


const initialState = [
    {
        '연도': '2023',
        '기업주가유지': 0,
        '기업주가감소10': 0,
        '기업주가감소20': 0,
    },{
        '연도': '2024',
        '기업주가유지': 0,
        '기업주가감소10': 0,
        '기업주가감소20': 0,
    },{
        '연도': '2025',
        '기업주가유지': 0,
        '기업주가감소10': 0,
        '기업주가감소20': 0,
    }
]

export const srimStockPriceSlice = createSlice({
    name: 'srimStockPrice',
    initialState,
    reducers: {
        srimStockPrice_input(state, action) {
            return action.payload
        },
        srimStockPrice_partInput(state, action) {
            state[0] = action.payload
        },
        srimStockPrice_reset(state){
            return initialState
        },
    },
})



// Action creators are generated for each case reducer function
export const { srimStockPrice_input,srimStockPrice_partInput, srimStockPrice_reset } = srimStockPriceSlice.actions

export default srimStockPriceSlice.reducer