import React, { useState } from 'react';
import { ResponsiveBar } from '@nivo/bar'
import Container from 'react-bootstrap/Container';
import { useSelector } from "react-redux";
import { useEffect } from 'react';
import Card from 'react-bootstrap/Card';



export default function SrimStockGraph() {

    const srimStockPrice = useSelector((state) => state.srimStockPrice)
    const fetchSrim = useSelector((state) => state.fetchSrim)

    let firstData = [
        {
            "연도": srimStockPrice[0]['연도'],
            "ROE 지속시": srimStockPrice[0]['적정주가유지'],
            "지속Color": "hsl(323, 70%, 50%)",
            "10%씩 감소": srimStockPrice[0]['적정주가감소10'],
            "10%씩 감소Color": "hsl(271, 70%, 50%)",
            "20%씩 감소": srimStockPrice[0]['적정주가감소20'],
            "20%씩 감소Color": "hsl(325, 70%, 50%)",
            "현재 주가": 0,
            "현재 주가Color": "hsl(73, 70%, 50%)",
        }, {
            "연도": srimStockPrice[1]['연도'],
            "ROE 지속시": srimStockPrice[1]['적정주가유지'],
            "지속Color": "hsl(323, 70%, 50%)",
            "10%씩 감소": srimStockPrice[1]['적정주가감소10'],
            "10%씩 감소Color": "hsl(271, 70%, 50%)",
            "20%씩 감소": srimStockPrice[1]['적정주가감소20'],
            "20%씩 감소Color": "hsl(325, 70%, 50%)",
            "현재 주가": 0,
            "현재 주가Color": "hsl(73, 70%, 50%)",
        }, {
            "연도": srimStockPrice[2]['연도'],
            "ROE 지속시": srimStockPrice[2]['적정주가유지'],
            "지속Color": "hsl(323, 70%, 50%)",
            "10%씩 감소": srimStockPrice[2]['적정주가감소10'],
            "10%씩 감소Color": "hsl(271, 70%, 50%)",
            "20%씩 감소": srimStockPrice[2]['적정주가감소20'],
            "20%씩 감소Color": "hsl(325, 70%, 50%)",
            "현재 주가": 0,
            "현재 주가Color": "hsl(73, 70%, 50%)",
        }
    ]

    const [stockData, setStockData] = useState(firstData)

    useEffect(() => {

        let resultData = [
            {
                "연도": srimStockPrice[0]['연도'],
                "ROE 지속시": srimStockPrice[0]['적정주가유지'],
                "지속Color": "hsl(323, 70%, 50%)",
                "10%씩 감소": srimStockPrice[0]['적정주가감소10'],
                "10%씩 감소Color": "hsl(271, 70%, 50%)",
                "20%씩 감소": srimStockPrice[0]['적정주가감소20'],
                "20%씩 감소Color": "hsl(325, 70%, 50%)",
                "현재 주가": Number(fetchSrim['주식정보']['시세종가']),
                "현재 주가Color": "hsl(73, 70%, 50%)",
            }, {
                "연도": srimStockPrice[1]['연도'],
                "ROE 지속시": srimStockPrice[1]['적정주가유지'],
                "지속Color": "hsl(323, 70%, 50%)",
                "10%씩 감소": srimStockPrice[1]['적정주가감소10'],
                "10%씩 감소Color": "hsl(271, 70%, 50%)",
                "20%씩 감소": srimStockPrice[1]['적정주가감소20'],
                "20%씩 감소Color": "hsl(325, 70%, 50%)",
                "현재 주가": Number(fetchSrim['주식정보']['시세종가']),
                "현재 주가Color": "hsl(73, 70%, 50%)",
            }, {
                "연도": srimStockPrice[2]['연도'],
                "ROE 지속시": srimStockPrice[2]['적정주가유지'],
                "지속Color": "hsl(323, 70%, 50%)",
                "10%씩 감소": srimStockPrice[2]['적정주가감소10'],
                "10%씩 감소Color": "hsl(271, 70%, 50%)",
                "20%씩 감소": srimStockPrice[2]['적정주가감소20'],
                "20%씩 감소Color": "hsl(325, 70%, 50%)",
                "현재 주가": Number(fetchSrim['주식정보']['시세종가']),
                "현재 주가Color": "hsl(73, 70%, 50%)",
            }
        ]

        setStockData(() => resultData)
    }, [srimStockPrice]) //useEffect가 리턴된 state는 인지하는데 부분교체된것은 못 알는듯


    const SrimStockGraph = ({ data }) => (
        <ResponsiveBar
            data={data}
            keys={[
                'ROE 지속시',
                '10%씩 감소',
                '20%씩 감소',
                '현재 주가',
            ]}
            indexBy="연도"
            margin={{ top: 50, right: 130, bottom: 50, left: 65 }}
            padding={0.3}
            groupMode="grouped"
            valueScale={{ type: 'linear' }}
            indexScale={{ type: 'band', round: true }}
            valueFormat=" >-,"
            colors={{ scheme: 'nivo' }}
            defs={[
                {
                    id: 'dots',
                    type: 'patternDots',
                    background: 'inherit',
                    color: '#38bcb2',
                    size: 4,
                    padding: 1,
                    stagger: true
                },
                {
                    id: 'lines',
                    type: 'patternLines',
                    background: 'inherit',
                    color: '#eed312',
                    rotation: -45,
                    lineWidth: 6,
                    spacing: 10
                }
            ]}
            fill={[
                {
                    match: {
                        id: 'fries'
                    },
                    id: 'dots'
                },
                {
                    match: {
                        id: 'sandwich'
                    },
                    id: 'lines'
                }
            ]}
            borderColor={{
                from: 'color',
                modifiers: [
                    [
                        'darker',
                        '1.6'
                    ]
                ]
            }}
            axisTop={null}
            axisRight={null}
            axisBottom={{
                tickSize: 5,
                tickPadding: 5,
                tickRotation: 0,
                legend: '연도',
                legendPosition: 'middle',
                legendOffset: 32
            }}
            axisLeft={{
                tickSize: 5,
                tickPadding: 5,
                tickRotation: 0,
                legend: '원',
                legendPosition: 'middle',
                legendOffset: -50
            }}
            labelSkipWidth={12}
            labelSkipHeight={16}
            labelTextColor={{
                from: 'color',
                modifiers: [
                    [
                        'darker',
                        1.6
                    ]
                ]
            }}
            legends={[
                {
                    dataFrom: 'keys',
                    anchor: 'bottom-right',
                    direction: 'column',
                    justify: false,
                    translateX: 120,
                    translateY: 0,
                    itemsSpacing: 2,
                    itemWidth: 100,
                    itemHeight: 20,
                    itemDirection: 'left-to-right',
                    itemOpacity: 0.85,
                    symbolSize: 20,
                    effects: [
                        {
                            on: 'hover',
                            style: {
                                itemOpacity: 1
                            }
                        }
                    ]
                }
            ]}
            role="application"
            ariaLabel="Nivo bar chart demo"
            barAriaLabel={function (e) { return e.id + ": " + e.formattedValue + " in 연도: " + e.indexValue }}
        />
    )


    return (
        <>
            <Container>
                <Card className="text-center" border="light">
                    <Card.Body>
                        <Card.Title>적정주가</Card.Title>
                        <Card.Text>
                        </Card.Text>
                        <div style={{ width: 'auto', height: '400px', margin: '0 auto' }}>
                            <SrimStockGraph data={stockData} />
                        </div>
                    </Card.Body>
                </Card>

            </Container>


        </>
    )
}
