import React, { useEffect, useState } from 'react';
import Nav from 'react-bootstrap/Nav';
import { useSelector } from 'react-redux';
import NavDropdown from 'react-bootstrap/NavDropdown';
import Tab from 'react-bootstrap/Tab';
import TestTable from './test/testTable';
import TestTable2 from './test/testTable2';
import MainSearch from './mainSearch';
import Col from 'react-bootstrap/Col';
import Row from 'react-bootstrap/Row';
import Container from 'react-bootstrap/esm/Container';
import Card from 'react-bootstrap/Card';

//페이지 컴포넌트
import OverView from './company/overview';
import Srim from './company/srim';


function SideBar() {

  //회사 이름 가져오기
  const corpName: string = useSelector((state: any) => state.corpName.value)
  const overView = useSelector((state: any) => state.overView)

  //회사를 입력해야만 활성화
  const [show기업개황, setShow기업개황] = useState<boolean>(true)
  //상장된 회사여야만 활성화
  const [showDisabled, setShowDisabled] = useState<boolean>(true)


  //외부링크
  const [재무제표링크, set재무제표링크] = useState('')
  const [차트링크, set차트링크] = useState('')
  const [기업공시링크, set기업공시링크] = useState('')
  const 상폐현황링크 = 'https://kind.krx.co.kr/investwarn/delcompany.do?method=searchDelCompanyMain'
  const 코스피상폐링크 = 'https://listing.krx.co.kr/contents/LST/04/04010500/LST04010500.jsp'
  const 코스닥상폐링크 = 'https://listing.krx.co.kr/contents/LST/04/04020500/LST04020500.jsp'
  const 코넥스상폐링크 = 'https://listing.krx.co.kr/contents/LST/04/04030500/LST04030500.jsp'



  useEffect(() => {
    //기업개활 활성화 조건
    if (overView.corp_cls === ''){
      setShow기업개황(true)
    } else {
      setShow기업개황(false)
    }

    //메뉴 활성화 조건
    if (overView.corp_cls === '' || overView.corp_cls === 'E') {
      setShowDisabled(true)
    } else {
      setShowDisabled(false)
    }
    
    if (overView.stock_code !== '') {
      set재무제표링크(() => `https://comp.fnguide.com/SVO2/ASP/SVD_Finance.asp?pGB=1&gicode=A${overView.stock_code}&cID=&MenuYn=Y&ReportGB=&NewMenuID=103&stkGb=701`)
    } else {
      set재무제표링크(() => 'https://comp.fnguide.com/SVO2/ASP/SVD_Finance.asp?pGB=1&gicode=A005930&cID=&MenuYn=Y&ReportGB=&NewMenuID=103&stkGb=701')
    }

    if (overView.stock_code !== '') {
      set차트링크(() => `https://finance.naver.com/item/main.naver?code=${overView.stock_code}`)
    } else {
      set차트링크(() => 'https://finance.naver.com/item/main.naver?code=005930')
    }

    if (overView.stock_code !== '') {
      set기업공시링크(() => `https://dart.fss.or.kr/html/search/SearchCompany_M2.html?textCrpNM=${overView.stock_code}`)
    } else {
      set기업공시링크(() => 'https://dart.fss.or.kr/html/search/SearchCompany_M2.html?textCrpNM=005930')
    }

  }, [overView])

  return (
    <Container>
      <Tab.Container id="left-tabs-example" defaultActiveKey="회사검색">
        <Row>
          <Col lg={2}>
            <Nav className="flex-column" variant="pills">
              <Nav.Link eventKey="회사검색">회사검색</Nav.Link>
              <Nav.Link eventKey="기업개황" disabled={show기업개황}>기업개황</Nav.Link>
              <Nav.Link onClick={() => { window.open(재무제표링크, '_blank') }} disabled={showDisabled}>재무제표</Nav.Link>
              <Nav.Link onClick={() => { window.open(차트링크, '_blank') }} disabled={showDisabled}>차트</Nav.Link>

              <NavDropdown title="가치평가" id="nav-dropdown" disabled={showDisabled}>
                <NavDropdown.Item eventKey="S-RIM">S-RIM</NavDropdown.Item>
              </NavDropdown>

              <Nav.Link onClick={() => { window.open(기업공시링크, '_blank') }} disabled={showDisabled}>기업공시</Nav.Link>
              <NavDropdown title="상장폐지요건" id="nav-dropdown">
                <NavDropdown.Item onClick={() => { window.open(코스피상폐링크, '_blank') }} >코스피</NavDropdown.Item>
                <NavDropdown.Item onClick={() => { window.open(코스닥상폐링크, '_blank') }}>코스닥</NavDropdown.Item>
                <NavDropdown.Item onClick={() => { window.open(코넥스상폐링크, '_blank') }}>코넥스</NavDropdown.Item>
                <NavDropdown.Divider />
                <NavDropdown.Item onClick={() => { window.open(상폐현황링크, '_blank') }}>상장폐지현황</NavDropdown.Item>
              </NavDropdown>
            </Nav>
          </Col>
          <Col lg="10">
            <Tab.Content>

              <Tab.Pane eventKey="회사검색">
                <MainSearch />
              </Tab.Pane>

              <Tab.Pane eventKey="기업개황">
                <OverView />
              </Tab.Pane>

              <Tab.Pane eventKey="S-RIM">
                <Srim/>
              </Tab.Pane>

            </Tab.Content>
          </Col>
        </Row>
      </Tab.Container>
    </Container>
  );
}

export default SideBar;